import { graphql } from 'gatsby'
import NewAppList from '../../components/newAppList'


export const query = graphql`
  query new201901 {
    thismonth: allAppminingresultsXlsxJanuary2019(
      filter: { Score_Last_Round: { eq: "0" }, Average: { ne: null } }
    ) {
      totalCount
      edges {
        node {
          appcoid: App_Id
          name: App_Name
          Final_Score
        }
      }
    }
    allApps {
      edges {
        node {
          ...AppInformation
          ...AppIcon
        }
      }
    }
  }
`

export default NewAppList({ month: 'January 2019' })
